import {followAPI, usersAPI} from "../API/apiRequests";
import {updateObjPropInArray} from "../helperFuncs/objectsInArray";

const SET_USERS = 'usersReducer/SET_USERS'
const SET_TOTAL_USERS_COUNT = 'usersReducer/SET_TOTAL_USERS_COUNT'
const SET_INITIALIZING_STATUS = 'usersReducer/SET_INITIALIZING_STATUS'
const SET_HAVE_ERROR = 'usersReducer/SET_HAVE_ERROR'
const DO_IS_FETCHING_TRUE = 'usersReducer/DO_IS_FETCHING_TRUE'
const DO_IS_FETCHING_FALSE = 'usersReducer/DO_IS_FETCHING_FALSE'
const FOLLOW_ON_USER = 'usersReducer/FOLLOW_ON_USER'
const UNFOLLOW_ON_USER = 'usersReducer/UNFOLLOW_ON_USER'
const DISABLE_CURRENT_BUTTON = 'usersReducer/DISABLE_CURRENT_BUTTON'
const ENABLE_CURRENT_BUTTON = 'usersReducer/ENABLE_CURRENT_BUTTON'

let initialState = {
    users: [],
    pageSize: 24,
    totalUsersCount: null,
    isFetching: false,
    followingOnProcess: [],
    hasError: false,
    initialized: false,
}

const userReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USERS:
            return {...state, users: [...action.users].sort((p, n) => p.id - n.id)}
        case SET_TOTAL_USERS_COUNT:
            return {...state, totalUsersCount: action.count}
        case SET_INITIALIZING_STATUS:
            return {...state, initialized: action.initialized}
        case SET_HAVE_ERROR:
            return {...state, hasError: true}
        case DO_IS_FETCHING_TRUE:
            return {...state, isFetching: true}
        case DO_IS_FETCHING_FALSE:
            return {...state, isFetching: false}
        case FOLLOW_ON_USER:
            return {...state,
                users: updateObjPropInArray(state.users, 'id', action.userId, {followed: true})}
        case UNFOLLOW_ON_USER:
            return {...state,
                users: updateObjPropInArray(state.users, 'id', action.userId, {followed: false})}
        case DISABLE_CURRENT_BUTTON:
            return {...state, followingOnProcess: [...state.followingOnProcess, action.userId]}
        case ENABLE_CURRENT_BUTTON:
            return {...state, followingOnProcess: state.followingOnProcess.filter(item => item !== action.userId)}
        default:
            return state
    }
}

const setUsers = (users) => ( {type: SET_USERS, users} )
const setTotalUsersCount = (count) => ( {type: SET_TOTAL_USERS_COUNT, count} )

const setInitializingStatus = (status) => ({type: SET_INITIALIZING_STATUS, initialized: status})
const setHaveError = () => ({type: SET_HAVE_ERROR})

const setIsFetchingTrue = () => ({type: DO_IS_FETCHING_TRUE})
const setIsFetchingFalse = () => ({type: DO_IS_FETCHING_FALSE})

const followOnUser = (userId) => ({type: FOLLOW_ON_USER, userId})
const unfollowOnUser = (userId) => ({type: UNFOLLOW_ON_USER, userId})

const disableCurrentButton = (userId) => ({type: DISABLE_CURRENT_BUTTON, userId})
const enableCurrentButton = (userId) => ({type: ENABLE_CURRENT_BUTTON, userId})

export const getUsers = (currentPage, pageSize) => async (dispatch) => {
    dispatch(setIsFetchingTrue())
    let response = await usersAPI.getUsers(currentPage, pageSize)
    if (!response.data.error) {
        dispatch(setUsers(response.data.items));
        dispatch(setIsFetchingFalse());
        dispatch(setTotalUsersCount(response.data.totalCount))
    } else {
        dispatch(setHaveError())
    }
    dispatch(setInitializingStatus(true))
}
export const follow = (id) => async (dispatch) => {
    dispatch(disableCurrentButton(id))
    let response = await followAPI.follow(id)
    if (response.data.resultCode === 0) {
        dispatch(followOnUser(id))
    }
    dispatch(enableCurrentButton(id))
}
export const unfollow = (id) => async (dispatch) => {
    dispatch(disableCurrentButton(id))
    let response = await followAPI.unfollow(id)
    if (response.data.resultCode === 0) {
        dispatch(unfollowOnUser(id))
    }
    dispatch(enableCurrentButton(id))
}


export default userReducer