import React, {useState} from "react";
import {connect} from "react-redux";
import {getAuthUserInfo, setAuthUser, userLogout} from "../../redux/authReducer";
import st from "./Header.module.css";
import Navbar from "./Navbar";
import defaultPhoto from "../../localImage/defaultUser.png";
import {NavLink} from "react-router-dom";
import {AiOutlineClose, AiOutlineMenu} from "react-icons/ai";
import {toggleMenuStatus} from "../../redux/appReducer";


const Header = ({login, isAuth, userLogout, photo, isMenuOpen, toggleMenuStatus}) => {

    const styles = isMenuOpen ? `${st.active_header} ${st.header_wrapper}` : st.header_wrapper
    return (
        <div className={isMenuOpen ? st.header_background_modal : ''}>

            <div className={styles}>
                <Navbar toggleMenuStatus={toggleMenuStatus}/>
                <AuthBlock isAuth={isAuth}
                           toggleMenuStatus={toggleMenuStatus}
                           userLogout={userLogout}
                           login={login}
                           photo={photo}
                />
            </div>
            <OpenMobileMenuButton isOpen={isMenuOpen}
                                  setOpenStatus={toggleMenuStatus}/>
        </div>
    )
}
let mapStateToProps = (state) => {
    return {
        login: state.authData.login,
        email: state.authData.email,
        isAuth: state.authData.isAuth,
        photo: state.authData.photos.small,
        isMenuOpen: state.app.isMenuOpen,
    }
}

export default connect(mapStateToProps, {getAuthUserInfo, userLogout, toggleMenuStatus})(Header)

const OpenMobileMenuButton = ({isOpen, setOpenStatus}) => {
    return (
        <button className={st.open_menu_button} onClick={() => {
            setOpenStatus()
        }}>
            {isOpen
                ? <AiOutlineClose color='white' size={'30px'}/>
                : <AiOutlineMenu color='white' size={'30px'}/>
            }
        </button>
    )

}

const AuthBlock = ({isAuth, userLogout, login, photo, toggleMenuStatus}) => {
    const handleClick = () => {
        toggleMenuStatus()
        userLogout()
    }
    return (
        <div className={st.auth_block}>
            <NavLink className={st.image_link} to={isAuth ? '/profile' : '/login'}
                     onClick={toggleMenuStatus}>
                <img src={photo || defaultPhoto} alt={'user image'}/>
            </NavLink>
            <span>{login || 'anonim'}</span>
            {(isAuth) ? <button onClick={handleClick}>logout</button> : <NavLink to={'/login'}>Log In</NavLink>}
        </div>
    )
}



