import st from './Resume.module.css'
import location_img from '../../localImage/resumeContacts/location.png'
import email_img from '../../localImage/resumeContacts/email (1).png'
import phone_img from '../../localImage/resumeContacts/telephone.png'
import {Links} from "./Links";




const Resume = () => {
    return (
        <div className={st.resume_wrapper}>
            <div className={st.resume_body}>
                <div className={st.name_block}>
                    <h2>Shamil<br/>Khanmagomedov</h2>
                    <h3>REACT DEVELOPER</h3>
                </div>
                <div className={st.contacts_block}>
                    <div className={st.basic_contacts}>
                        <div>
                            <img src={location_img}/>
                            <p>Turkey, Alanya</p>
                        </div>
                        <div>
                            <img src={phone_img}/>
                            <p>+90 501 063 90 66</p>
                        </div>
                        <div>
                            <img src={email_img}/>
                            <p>Khanmag329@gmail.com</p>
                        </div>
                    </div>
                    <div className={st.links_block}>
                        <p>#LINKS</p>
                        <Links />
                    </div>
                </div>
                <div className={st.hard_skills}>
                    <p>React</p>
                    <p>Redux</p>
                    <p>Node</p>
                    <p>JavaScript</p>
                    <p>HTML</p>
                    <p>CSS</p>
                    <p>Python</p>
                    <p>Git</p>
                    <p>English B1</p>
                    <p>React Router</p>
                    <p>Formik</p>
                </div>
                <div className={st.experiense}>
                    <div>
                        <h4>Web developer</h4>
                    </div>
                </div>
            </div>

        </div>
    )
}


export default Resume