import git_icon from '../../localImage/contacts_logo/github.png'
import whatsapp_icon from '../../localImage/resumeContacts/whatsapp.png'
import telegram_icon from '../../localImage/resumeContacts/telegram.png'
import my_web_icon from '../../localImage/resumeContacts/responsive.png'
import linkedin_icon from '../../localImage/resumeContacts/linkedin.png'
import st from './Resume.module.css'

const links = [
    {name: 'my own web site', link: '', image: my_web_icon},
    {name: 'whatsapp', link: '', image: whatsapp_icon},
    {name: 'telegram', link: '', image: telegram_icon},
    {name: 'github', link: '', image: git_icon},
    {name: 'Linked In', link: 'linkedin.com/in/--44283325b', image: linkedin_icon},
]
const Links = () => {
    return (
        <div className={st.links}>
            {
                links.map(item => (
                    <a href={item.link}>
                        <img alt={item.name} src={item.image} title={item.name}/>
                    </a>
                ))
            }

        </div>
    )
}
export {Links}