import {NavLink} from "react-router-dom";
import st from './Header.module.css'

const Navbar = ({toggleMenuStatus}) => {
    const navbarData = [
        {name: "Profile", link: "/profile"},
        {name: "Dialogs", link: "/dialogs"},
        {name: "Users", link: "/users"},
        {name: "Resume", link: "/resume"},
    ]

    return <nav className={st.nav}>
        {navbarData.map((item) => (
            <CreateNavLink toggleMenuStatus={toggleMenuStatus}
                           name={item.name}
                           link={item.link}
                           key={item.name}
            />
        ))}
    </nav>
}
export default Navbar


const CreateNavLink = ({name, link, toggleMenuStatus}) => {
    return (
        <NavLink to={link}
                 className={({isActive}) => isActive ? (st.active_link + " " + st.nav_link) : st.nav_link}
                 onClick={toggleMenuStatus}
        >
            {name}
        </NavLink>
    )
}